	import uCharts from '@/components/u-charts/u-charts.js';
	var _self;
	var canvaLineA=null;
  export default {
    data () {
      return {
        weekData:[],
		url:"",
		cWidth:'',
		cHeight:'',
		pixelRatio:1,
       
      }
    },
	onLoad(options) {
		this.url=this.$consturl;
		_self = this;
		this.cWidth=uni.upx2px(750);
		this.cHeight=uni.upx2px(500);
		// console.log(this);
		this.getServerData(options.deviceid);
	},
    methods:{
		getServerData(deviceid){
			let that=this,lineData;
			  uni.request({
			  url: that.url+'web/api/findHistoryReportWeek',
			  header: {
			    'content-type': 'application/json'
			  },
			  data: {deviceid:deviceid},
			  method: 'get',
			  success: function(res){
			    that.weekData=res.data;
			    let LineA={categories:[],series:[]};
				let asdasda={"name": "","data":""};
			    let lineD=res.data.findTime;
			    let o=[];
			  o[0]=lineD.findTime7_1;o[1]=lineD.findTime7_2;o[2]=lineD.findTime7_3;o[3]=lineD.findTime7_4;o[4]=lineD.findTime7_5;o[5]=lineD.findTime7_6;o[6]=lineD.findTime7_7;
			  lineData=o;
			  asdasda.data=lineData;
			  asdasda.name="每天的上网时长";
			  LineA.categories=['周一', '周二', '周三', '周四', '周五', '周六', '周日']
			  LineA.series.push(asdasda);
			  _self.showLineA("canvasLineA",LineA);
			  },
			  fail: function() {
			  },
			  complete: function() {
			  }
			});
		},
		showLineA(canvasId,chartData){
			canvaLineA=new uCharts({
				$this:_self,
				canvasId: canvasId,
				type: 'line',
				fontSize:11,
				legend:{show:true},
				dataLabel:false,
				dataPointShape:true,
				background:'#FFFFFF',
				pixelRatio:_self.pixelRatio,
				categories: chartData.categories,
				series: chartData.series,
				animation: true,
				xAxis: {
					type:'grid',
					gridColor:'#CCCCCC',
					gridType:'dash',
					dashLength:8
				},
				yAxis: {
					gridType:'dash',
					gridColor:'#CCCCCC',
					dashLength:8,
					splitNumber:5,
					min:10,
					max:180,
					format:(val)=>{return val.toFixed(0)+'秒'}
				},
				width: _self.cWidth*_self.pixelRatio,
				height: _self.cHeight*_self.pixelRatio,
				extra: {
					line:{
						type: 'straight'
					}
				}
			});
			
		},
		touchLineA(e) {
			canvaLineA.showToolTip(e, {
				format: function (item, category) {
					return category + ' ' + item.name + ':' + item.data 
				}
			});
		},
        fh:function() {
              console.log(123);
              const url = "../history/main";
              uni.navigateTo({ url });
            }
    },
    onShareAppMessage () {}
  }
